.TodoItem {
  padding: 1rem;
  display: flex;
  align-items: center; // 세로 중앙 정렬
  &:nth-child(even) {
    background: #f8f9fa;
  }

  .checkbox {
    cursor: pointer;
    flex: 1; // 차지할 수 있는 영역 모두 차지
    display: flex;
    align-items: center; // 세로 중앙 정렬
    svg {
      // 아이콘
      font-size: 1.5rem;
    }
    .title {
      margin-left: 0.5rem;
      flex: 1; // 차지할 수 있는 영역 모두 차지
      font-size: 20px;
    }
    .content {
      margin-left: 0.7rem;
      color: #777777;
      flex: 1;
      font-size: 16px;
    }
    // 체크되었을 때 보여줄 스타일
    &.checked {
      svg {
        color: #22b89f;
      }
      .title {
        color: #adb5bd;
        text-decoration: line-through;
      }
      .content {
        color: #adb6b5;
        text-decoration: line-through;
      }
    }
  }
  .modify {
    display: flex;
    align-items: center;
    font-size: 1.5rem;
    color: #6b6b99;
    margin-left: 10px;
    margin-right: 10px;
    cursor: pointer;
    &:hover {
      color: #7b7baa;
    }
  }

  .remove {
    display: flex;
    align-items: center;
    font-size: 1.5rem;
    margin-left: 10px;
    margin-right: 10px;
    color: #ff6b6b;
    cursor: pointer;
    &:hover {
      color: #ff8787;
    }
  }

  // 엘리먼트 사이사이에 테두리를 넣어줌
  & + & {
    border-top: 1px solid #dee2e6;
  }
}
